import React from "react";
import { LinkContainer } from "react-router-bootstrap";
import { NavDropdown, NavLink } from "react-bootstrap";

import { Navbar, Nav, Container } from "react-bootstrap";

import clientLogo from "../images/qldc.png";
import stantecLogo from "../images/stantec.svg";

const Header = () => {
  return (
    <Navbar
      expand="lg"
      variant="dark"
      fixed="top"
      style={{ background: "#00355f", fontFamily: "MyFont" }}
    >
      <Container fluid>
        <Navbar.Brand>
          <div>
            <img src={clientLogo} alt="QLDC logo" height={40} />
            <span style={{ paddingLeft: "15px", fontSize: "1.5rem" }}>
              Minor Improvement Programme 2021-2024 : &nbsp;{" "}
              <i>
                <strong>Delivery Framework</strong>
              </i>
            </span>
          </div>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="justify-content-end"
            style={{ width: "100%", fontSize: "1.2rem" }}
          >
            <LinkContainer to="/home">
              <NavLink>Home</NavLink>
            </LinkContainer>
            <LinkContainer to="/delivery-team">
              <NavLink>Delivery Team</NavLink>
            </LinkContainer>
            <LinkContainer to="/framework">
              <NavLink>The Framework</NavLink>
            </LinkContainer>
            <LinkContainer to="/documents">
              <NavLink>Documents</NavLink>
            </LinkContainer>
            <LinkContainer to="/reporting">
              <NavLink>Reporting</NavLink>
            </LinkContainer>
            <LinkContainer to="/map">
              <NavLink>Sites Map</NavLink>
            </LinkContainer>
            <NavDropdown title="Dropdown">
              <NavDropdown.Item>
                <LinkContainer to="/reporting"  style={{
                      "&:hover": {
                        backgroundColor: "red",
                      },
                    }}>
                  <NavLink
                    
                  >
                    Reporting
                  </NavLink>
                </LinkContainer>
              </NavDropdown.Item>
              <NavDropdown.Item>Another action</NavDropdown.Item>
              <NavDropdown.Item>Something</NavDropdown.Item>
            </NavDropdown>
            <img
              src={stantecLogo}
              alt="Stantec logo"
              height={35}
              style={{ paddingLeft: "40px" }}
            />
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
