import React from 'react';
import { Container } from 'react-bootstrap';
import styled from 'styled-components';

const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50A712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;

`;

const requestOptions = {
    method: 'POST',
    mode :'no-cors' ,
    headers: { 'Content-Type': 'application/json', 'referer': "https://nz.mwhglobal.com"}
};

fetch('https://apps.ramm.co.nz/RammApi6.1/v1/authenticate/login?database=SH%20New%20Zealand&userName=dhpj&password=d0y0urbest', requestOptions)
    .then(async response => {
        const isJson = response.headers.get('content-type')?.includes('application/json');
        const data = isJson && await response.json();
        console.log(data)
        // check for error response
        if (!response.ok) {
            // get error message from body or default to response status
            const error = (data && data.message) || response.status;
            return Promise.reject(error);
        }

        console.log(data)
    })
    .catch(error => {
        console.error('There was an error!', error);
    });



const Map = () => {
  return (
    <Container fluid style={{ marginTop: "66px", minHeight: "100vh"}}>
      <TitleText>Map</TitleText>
      <hr/>
      <NormalText>
        In line with the government's <a href="https://www.nzta.govt.nz/safety/what-waka-kotahi-is-doing/nz-road-safety-strategy/">Road to Zero</a> safety targets, the Queenstown Lakes District Council (QLDC) is spending <strong>$25M throughout 2021 to 2024 </strong>to implement the <i>Minor Improvement Programme</i>.
        <br/>This is a much larger programme of work than in previous years and will require structure, visibility and accountability to ensure the required safety and value for money  outcomes are achieved. 
      </NormalText>

    </Container>

  );
};

export default Map;
